
.button {
  width: 72px;
  height: 32px;
  border: none;
  cursor: pointer;
  font-size: 20px;
  background: #ddd;
  color: #222;
  text-decoration: none;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  vertical-align: middle;
  margin-right: .5em;
}
.button:hover {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}
.button.primary {
    background: #02af5f;
    color: #fff;
}
#topbar {
  background: #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
}
#topbar > .wrapper {
    margin: 0 auto;
    height: 64px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0 16px;
}
#topbar > .wrapper .error {
      color: red;
}
#topbar > .wrapper .userActions {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
#topbar > .wrapper .userActions .welcome, #topbar > .wrapper .userActions .logo {
        margin-right: 1em;
        font-size: 24px;
        color: #000;
}



